.about {
  position: relative;
  z-index: -1;
  span {
    position: absolute;
    bottom: -60%;
    right: -5%;
    z-index: -1;
    content: "";
    display: inline-block;
    width: 350px;
    height: 350px;
    background: $c200;
    filter: blur(155px);
  }
  &:after {
    position: absolute;
    content: "";
    background-image: url("/img/about-el-2.png");
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 400px;
    top: -25%;
    overflow: hidden;
  }
  &:before {
    position: absolute;
    top: -15%;
    left: -18%;
    z-index: -1;
    content: "";
    display: inline-block;
    width: 350px;
    height: 350px;
    background: $c200;
    filter: blur(155px);
  }
  &-inner {
    padding-top: 118px;
    &__title {
      h2 {
        font-size: 44px;
        line-height: 50px;
        font-weight: var(--font-bk);
      }
    }
    &__cont {
      margin-top: 57px;
      ul {
        li {
          position: relative;
          display: flex;
          justify-content: space-between;
          &:after {
            position: absolute;
            content: "";
            background-image: url("/img/svg/about-el-1.svg");
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            display: inline-block;
            top: -48%;
            right: -6%;
            width: 220px;
            height: 220px;
          }
          p {
            font-weight: var(--font-r);
            line-height: 25px;
            width: 700px;
          }
        }
      }
    }
    &__bottom {
      margin: 60px 0 100px;
      ul {
        li {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
      }
      &--text {
        width: 395px;
        h2 {
          font-size: 24px;
          line-height: 30px;
          font-weight: var(--font-b);
          margin-bottom: 17px;
        }
        p {
          line-height: 25px;
          font-weight: var(--font-r);
        }
        p + p {
          margin-top: 20px;
        }
      }
    }
  }
}
